
.home-con{
	width:50%;
	margin:10% 25% 0;
	height:auto;
	text-align: center;
	font-family: 'Microsoft YaHei';
}

.home-con img{
	margin:0;
	width:180px;
	height:180px;
	border:2px solid #ccc;
	border-radius: 50%;
}

.home-con h3{
	color:#558866;
	font-weight: normal;
	font-size: 25px;
}

.home-con p{
	margin:0;
	margin-top:20px;
}

.home-con a{
	display: inline-block;
	color: #558866;
	margin:0 10px;
	background:#eee;
	padding:2px 5px;
	border-radius: 5px;
	text-decoration: none;
	-webkit-box-shadow: 2px 2px 5px #333;
	        box-shadow: 2px 2px 5px #333;
}





